*, *::before, *::after {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
  background-color: #f6f8fa; }

p {
  text-align: justify; }

a {
  text-decoration: none; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.header {
  height: 6.4rem;
  display: flex;
  align-items: center;
  background-color: #682444; }

.nav {
  padding: 0 2.4rem; }
  .nav .logo {
    display: flex;
    color: #FFF;
    font-size: 3rem; }

.upload-section {
  background-color: #FFF;
  border-radius: 1rem;
  padding: 3.2rem;
  margin-top: 1.6rem;
  border: 1px solid #e1e4e8; }
  .upload-section h2 {
    margin-top: 0;
    text-align: center; }
    @media (min-width: 600px) {
      .upload-section h2 {
        text-align: center; } }
  .upload-section .field:not(:last-of-type) {
    margin: 3.2rem 0; }
    @media (min-width: 600px) {
      .upload-section .field:not(:last-of-type) {
        margin-top: 0;
        margin-bottom: 0; } }

@media (min-width: 600px) {
  .form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3.2rem; } }

@media (min-width: 600px) {
  .form .field p {
    text-align: center; } }

.select-input {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #fafbfc;
  color: #0366d6;
  width: 100%;
  margin: 0 auto;
  height: 3.2rem;
  padding-left: 1.6rem;
  cursor: pointer; }
  @media (min-width: 600px) {
    .select-input {
      width: 15rem; } }
  .select-input::after {
    content: "";
    position: absolute;
    top: 1.33333rem;
    right: 1.6rem;
    border: 0.53333rem solid;
    border-color: #676f77 transparent transparent transparent;
    cursor: pointer;
    transition: 0.5s ease-in-out; }
  .select-input:hover, .select-input.selected {
    background-color: #f3f4f6; }

.month-container {
  display: grid;
  grid-template-columns: repeat(4, 4.8rem);
  grid-template-rows: repeat(3, 4.8rem);
  border: 1px solid #e1e4e8;
  border-radius: 1rem;
  background-color: #f3f4f6;
  width: fit-content;
  margin: 0 auto 0 auto;
  height: 0;
  overflow: hidden;
  opacity: 0.01;
  transition: 0.5s; }
  .month-container input {
    width: 1px;
    height: 1px;
    display: inline-block;
    position: absolute;
    opacity: 0.01; }
  .month-container label {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
    .month-container label:nth-of-type(1) {
      border-top-left-radius: 1rem; }
    .month-container label:nth-of-type(4) {
      border-top-right-radius: 1rem; }
    .month-container label:nth-of-type(9) {
      border-bottom-left-radius: 1rem; }
    .month-container label:nth-of-type(12) {
      border-bottom-right-radius: 1rem; }
    .month-container label:hover {
      background-color: #0366d6;
      color: #FFF; }
  .month-container input:checked + label {
    background-color: #0366d6;
    color: #FFF; }

.select-input.selected + .month-container {
  margin-top: 0.8rem;
  opacity: 1;
  height: 14.4rem; }

.select-input.selected::after {
  transform: rotate(180deg);
  top: 0.8rem; }

.upload-input-container {
  position: relative; }
  .upload-input-container input {
    width: 0px;
    height: 0px;
    position: absolute;
    opacity: 0.01s; }
  .upload-input-container .flex-upload-container {
    display: flex;
    justify-content: center; }
  .upload-input-container label {
    padding: 0.7rem 1.6rem;
    background-color: #682444;
    color: #FFF;
    font-weight: 100;
    border-radius: 1rem;
    cursor: pointer; }
    .upload-input-container label:hover {
      background-color: #42172b; }

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #682444;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #682444 transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.table-container {
  padding: 4.8rem; }

.person-table {
  width: 100%;
  border-spacing: 0;
  border: 1px solid #e1e4e8;
  border-radius: 1rem; }
  .person-table .row-title {
    background-color: #682444;
    color: #FFF; }
    .person-table .row-title th {
      font-weight: 400;
      font-size: 2rem;
      padding: 0.9rem 0; }
      .person-table .row-title th:first-of-type {
        border-top-left-radius: 1rem; }
      .person-table .row-title th:last-of-type {
        border-top-right-radius: 1rem; }
  .person-table .data-row:nth-of-type(2n+1) {
    background-color: #d5dfe8; }
  .person-table .data-row:nth-of-type(2n) {
    background-color: #FFF; }
  .person-table .data-row:last-of-type td:first-of-type {
    border-bottom-left-radius: 1rem; }
  .person-table .data-row:last-of-type td:last-of-type {
    border-bottom-right-radius: 1rem; }
  .person-table .data-row td {
    padding: 0.7rem 0;
    text-align: center; }
